export default function RulesSumary() {
  return (
    <section className="px-6 py-16 mx-10 my-10 font-sans text-white bg-[#291309]">
      <div className="max-w-4xl mx-auto text-left">
        {/* Nadpis */}
        <h2 className="mb-4 text-3xl font-extrabold tracking-wide text-center">
          Zhrnutie hry
        </h2>
        

        {/* Obsah */}
        <div className="pt-6 space-y-6 text-base leading-relaxed text-gray-200 border-t border-gray-600">
          {/* Cie hry */}
          <ul className="pl-5 space-y-2 list-disc">

            <li>  Kad hr pouva vlastn balek obsahujci presne 30 kariet. 
                <ul className="pl-6 mt-2 space-y-1 text-gray-300 list-disc">
                  <li> V balku mu by najviac tri kpie rovnakej karty, priom mus obsahova aspo p <em>Bench</em> kariet. </li>
                  <li> Set kariet s raritou <em>Vzcna</em> a <em>Zzran</em> nesmie presiahnu desa. </li>
                  <li> Kad <em>Zzran</em> kartu mono zaradi do balka iba v jednej kpii. </li>
                </ul>
            
            </li>

           

            <li>
              Zaiatok hry:
              <ul className="pl-6 mt-2 space-y-1 text-gray-300 list-disc">
                <li>Kad hr si dkladne zamiea svoj balek a polo ho lcom nadol a hri si uria, kto zane ako prv.</li>
                <li>Zanajci hr si potiahne 3 karty zo svojho balka na ruku a druh hr si potiahne 4 karty.</li>
                <li>Nsledne hra zana ahom zanajceho hra.</li>
              </ul>
            </li>

            <li>
              Hri sa striedaj v ahoch. Poas svojho ahu me hr vykona jednu z nasledujcich akci:
              <ul className="pl-6 mt-2 space-y-1 text-gray-300 list-disc">
                <li>Zahra 1 kartu z ruky a vyriei jej efekt; alebo</li>
                <li>
                  Potiahnu 1 kartu z balka a nsledne urobi jednu z volitench akci:
                  <ul className="list-[circle] pl-6 mt-1 space-y-1">
                    <li>presun jednu svoju kartu na ploche do vhodnho stpca; alebo</li>
                    <li>otoi jednho zo svojich svtcov na uenka.</li>
                  </ul>
                </li>
              </ul>
              <p className="mt-3 italic text-gray-300">
                Pravidlo przdnej ruky: ak m hr na zaiatku svojho ahu 0 kariet na ruke, potiahne si namiesto benej akcie dve karty z
                balka.
              </p>
            </li>

            <li>
              Uenci: 
              <ul className="pl-6 mt-2 space-y-1 text-gray-300 list-disc">
              <li> Karta otoen na rubov stranu sa povauje za uenka a predstavuje jeden symbol v ubovonej kategrii
              cnost. </li>
              </ul>
            </li>

            <li>
              Vazstvo a prehra:
              <ul className="pl-6 mt-2 space-y-1 text-gray-300 list-disc">
                <li>Hr vyhrva, ak ako prv zskal sedem symbolov v kadej z aspo troch rznych kategri.</li>
                <li>Hr prehrva, ak nem iadne karty na ruke a zrove je jeho balek przdny.</li>
              </ul>
            </li>

            
          </ul>
        </div>
      </div>
    </section>
  );
}
